
import React, { Component } from 'react';
import Store from './../stores/main.js'
import { 
  JsonPathToValue, 
  Trim, 
  ReturnCustomHeaders, 
  RemoveObjectsWithDuplicateProperty
} from './../utils/Core';
import { ReturnFriendlyErrorMessage } from './../utils/Text';
import {
  FetchJSON,
  Centre,
  ErrorComponent,
  Caterpillar
} from './_Common';
import OrderItemLayout from './../layouts/OrderItem';

const API_HOST = process.env.NODE_ENV === 'test' ? process.env.REACT_APP_API_HOST_TEST : process.env.REACT_APP_API_HOST_PRODUCTION
const API_ENDPOINT = "/orders/";
const DATA_PATH = "result";

export default class OrderItem extends Component {

  constructor(props) {
    super(props)
    this._isMounted = false
    this.state = {
      errorHeadline: null,
      errorDescription: null,
      isLoaded: false,
      data: null
    }
  }

  returnAPIUrl() {
    return API_HOST
      + API_ENDPOINT
      + Trim(this.props.match.params.id)
      + '/' + Trim(this.props.match.params.itemId)
      + '?postCode=' + Trim(this.props.match.params.postcode)
  }

  setCustomHeaders() {
    return this.customHeaders = ReturnCustomHeaders()
  }

  componentDidMount() {
    this._isMounted = true

    let state = Store.getOrderItemById(Trim(this.props.match.params.itemId))
    if (state) {
      this.setState(state)
      return
    }

    //let url =  this.returnAPIUrl()
    let url = 'https://api.ee.co.uk/bt-business/v1/bundle-orders/BT4ZV7RF/BT4ZV7RF1-10'
    let customHeaders = this.setCustomHeaders()

    FetchJSON(url, customHeaders)
      .then(
        (response) => {
          if(!response.isSuccess) {
            throw new Error(response.errorMessage)
          }
          if (!this._isMounted) return;
          return JsonPathToValue(response, DATA_PATH)
        },
        (error) => {
          throw new Error(error)
        })
      .then(data => {
        this.onFetch(data)
      })
      .catch(error => {
        this.handleError(error.message)
      })
  }

  onFetch (data) {
    if (!this._isMounted) return
    let uniqueOrderItems = RemoveObjectsWithDuplicateProperty(data.ProductOrderItems.OrderItems, 'DisplayName')
    let uniqueOrderItemsFree = this.returnFreeItems(uniqueOrderItems)
    data.ProductOrderItems.OrderItems = uniqueOrderItemsFree // overwrite original
    this.setState({
      isLoaded: true,
      data: data
    })
  }

  isFreeService(item) {
    return ( item.OneOffPrice === 0 && item.MonthlyPrice === 0 && item.QuarterlyPrice ) ? item : null
  }

  returnFreeItems(items) {
    return items.map(this.isFreeService)
  }

  handleError(error) {
    if (!this._isMounted) return
    let message = ReturnFriendlyErrorMessage(error)
    this.setState({ 
      isLoaded: true,
      errorDescription: message,
      errorDebugging: this.customHeaders
    })
  }

  componentWillUnmount() {
    if (this.state.data) {
      let orderItemReference = this.state.data.ProductSummary.OrderItemReference
      Store.saveOrderItem(orderItemReference, this.state)
    }
    this._isMounted = false
  }

  render() {
    const { isLoaded, data, errorDescription, errorDebugging } = this.state
    if (errorDescription) {
      return  <ErrorComponent 
                description={errorDescription} 
                errorDebugging={errorDebugging} 
              />
    } else if (!isLoaded) {
      return <Centre><Caterpillar /></Centre>
    } else {
      return <OrderItemLayout {...this.props} {...data} />
    }
  }
}



