import React, { Component } from 'react'
import { RemoveWhitespace } from './../utils/Core'
import { isEmpty, isInvalidPostcode } from './../utils/Validations'
import Form from './../modules/Form'
import FormControl from './../components/FormControl'
import Tooltip from './../components/Tooltip'


class OrderReferenceForm extends Component {
  constructor(props) {
    super(props)
    this.state = {
      postcodeHasError: false
    }
  }

  onPostcodeBlur(event) {
    let value = RemoveWhitespace(event.target.value)
    let isInvalid = (!isEmpty(value) && isInvalidPostcode(value) )
    this.setState({ 
      postcodeHasError: isInvalid
    })
  }

  render() {
    return (
      <Form onSubmit={ this.props.onOrderReferenceSubmit } 
        postcodeHasError={this.state.postcodeHasError} >
        <div className="grid-x grid-margin-x">
          <div className="cell large-5">
            <div className="form-control">
              <label>Order reference <Tooltip 
                  id="OrderReferenceNumberHelp"
                  text="You can find your order reference number near the top of your order confirmation email or letter from BT. Most common formats are BTA12345 or
    GBT12345."
              />
                <input 
                name="OrderReferenceNumber" 
                type="text" 
                placeholder="e.g. BTA12345" 
                aria-describedby="OrderReferenceNumberHelp" 
                className="uppercase"
                ref={this.props.orderReferenceInputRef} />
              </label>
            </div>
          </div>
          <div className="cell large-5">
            <FormControl 
              type = 'text'
              label = 'Postcode - billing, service or shipping' 
              placeholder = 'EC1A 7AJ (optional)'
              name = 'OrderPostcode' 
              errorText = "The postcode you entered doesn't appear to be valid."
              hasError = {this.state.postcodeHasError} 
              onInputBlur = { this.onPostcodeBlur.bind(this) }
              inputClassname= 'uppercase'
              helpText = 'Without a postcode you will not be able to see some order details and will not be able to make changes.'
              inputRef= {this.props.postcodeInputRef}
              scrollIntoView = {true}
              />
          </div>
          <div className="cell large-2 padding-top-29-medium">
            <input type="submit" className="button" value={ this.props.buttonText } />
          </div>
        </div>  
      </Form>
    )}
  }

export default OrderReferenceForm

