
import React from 'react'
import styled from 'styled-components'
import uniqueId from 'react-html-id'
import Disc from './Disc.js'
import InfoIkon from './Ikons/Info.js'
import Popover from './../modules/Popover.js'

const TooltipWrapper = styled.span`
  position: relative;
  padding: 10px 20px 20px 4px
`

export default class Tooltip extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      isOpen: false
    }
    this.handleFocus = this.handleFocus.bind(this)
    this.handleBlur = this.handleBlur.bind(this)
    this.handleClick = this.handleClick.bind(this)
    uniqueId.enableUniqueIds(this)
  }

  handleFocus() {
    this.setState({
      isOpen: true
    })
  }

  handleClick() {
    this.setState({
      isOpen: true
    })
  }

  handleBlur(event) {
    event.stopPropagation()
    this.setState({
      isOpen: false
    })
  }

  render() {
    return (
      <TooltipWrapper>
        <button ref={this.button}
          aria-controls={ this.props.id }
          aria-haspopup="true" 
          aria-expanded={this.state.isOpen ? "true" : "false"} 
          onFocus={this.handleFocus}
          onBlur={this.handleBlur}
          onClick={this.handleClick}
          type='button'
          tabIndex='0'>
          <Disc fill={"#333"} width={15} height={15}>
            <InfoIkon fill={"#fff"} width={4} height={12} />
          </Disc>
        </button>
        <Popover 
          id={this.props.id}
          text={this.props.text} 
          role= "tooltip"
          className="top" 
          headline="Headline" 
          icon="alert"
          isOpen={this.state.isOpen}
          ariaHidden={this.state.isOpen} />
      </TooltipWrapper>
    )
  }
}

