
import React, { Component } from 'react';
import Store from './../stores/main.js'
import { JsonPathToValue, Trim, ReturnCustomHeaders } from './../utils/Core';
import { ReturnFriendlyErrorMessage } from './../utils/Text';
import {
  FetchJSON,
  Centre,
  ErrorComponent,
  Caterpillar
} from './_Common';
import GroupOrderLayout from './../layouts/GroupOrder';

const API_HOST = process.env.NODE_ENV === 'test' ? process.env.REACT_APP_API_HOST_TEST : process.env.REACT_APP_API_HOST_PRODUCTION

const API_ENDPOINT = "/group-orders/"
const DATA_PATH = "result.Orders"

export default class GroupOrder extends Component {

  constructor(props) {
    super(props);
    this._isMounted = false;
    this.state = {
      errorDescription: null,
      isLoaded: false,
      items: null
    };
  }

  returnAPIUrl() {
    return API_HOST 
      + API_ENDPOINT 
      + Trim(this.props.match.params.id)
  }

  setCustomHeaders() {
    return this.customHeaders = ReturnCustomHeaders()
  }

  componentDidMount() {
    this._isMounted = true

    let id = Trim(this.props.match.params.id)
    let state = Store.getGroupOrderById(id)
    if (state) {
      this.setState(state)
      return
    }

    let url = this.returnAPIUrl()
    let customHeaders = this.setCustomHeaders()

    FetchJSON(url, customHeaders)
      .then(
        (response) => {
          if(!response.isSuccess) {
            throw Error(response.errorMessage);
          }
          if (!this._isMounted) return;
          return JsonPathToValue(response, DATA_PATH)
        },
        (error) => {
          throw Error(error);
        }
      )
      .then(data => {
        this.onFetch(data)
      })
      .catch(error => {
        this.handleError(error.message)
      })
  }


  onFetch (data) {
    if (!this._isMounted) return
    this.setState({
      isLoaded: true,
      items: data
    })
  }

  handleError(error) {
    if (!this._isMounted) return
    let message = ReturnFriendlyErrorMessage(error)
    this.setState({ 
      isLoaded: true,
      errorDescription: message,
      errorDebugging: this.customHeaders
    })
  }

  componentWillUnmount() {
    let id = Trim(this.props.match.params.id)
    if (this.state.isLoaded) {
      Store.saveGroupOrder(id, this.state)
    }
    this._isMounted = false;
  }

  render() {
    const { isLoaded, items, errorDescription, errorDebugging } = this.state
    if (errorDescription) {
      return  <ErrorComponent 
          description={errorDescription} 
          errorDebugging={errorDebugging} 
        />
    } else if (!isLoaded) {
      return <Centre><Caterpillar /></Centre>
    } else {
      return <GroupOrderLayout {...this.props} items={items} />
    }
  }
}



