import React from 'react'
import classNames from 'classnames'
import PropTypes from 'prop-types'
import './Popover.css'

const { string, bool } = PropTypes

export default function Popover(props) {
  const { className, isOpen } = props
  let popoverClass = classNames({
    'popover': true,
    'visually-hidden': !isOpen
  })
  return (
    <span 
      id = { props.id }
      className={ popoverClass + ` ` + className } 
      role={ props.tooltip } 
      aria-hidden={ props.isOpen ? 'false' : 'true' }>
      <span className="popover__body">
        <span className="popover__icon icon-alert-white" role="img" aria-label="Alert">&nbsp;</span>
        <p className="popover__title"><b>{ props.title }</b></p>
        <p className="popover__text">{ props.text }</p>
      </span>
    </span>
  )
}

Popover.propTypes = {
  className: string,
  id: string,
  title: string,
  text: string,
  isOpen: bool,
  position: string
}

Popover.defaultProps = {
  className: 'top',
  id: 'popover',
  title: '',
  text: '',
  isOpen: false,
  position: 'top'
}



