import React from 'react';

const Chat = (props) => (
  <section className={ `off-black ${props.className}` } style={{
    backgroundColor: 'black',
    color: 'white',
    padding: '30px 0'}}>
    <div className="grid-container">
      <div className="grid-x grid-margin-x">
        <div className="cell large-4 xlarge-6">
          <h3 className="h3">Problem with your order?</h3>
          <p>Get in touch with our team if you have any questions about your order or would like to make a change</p>
        </div>
        <div className="cell large-4 xlarge-4">
          <p>Open 08.00 - 18.00 Monday-Friday <br />
          Or request a callback</p>
        </div>
        <div className="cell large-4 xlarge-2 align-right-medium">
          <button className="button">Live chat</button><br />
          <span className="label white outline">Currently open</span>
        </div>
      </div>
    </div>
  </section>
);

export default Chat

