import React, { Component } from 'react'
import { isEmpty } from './../utils/Validations'
import classNames from 'classnames'
import OptionallyDisplayed from './../utils/OptionallyDisplayed.js'
import Popover from './../modules/Popover.js'
import PropTypes from 'prop-types'
import './FormControl.css'

export default class FormControl extends Component {

  constructor(props) {
    super(props)
    this.state = {
      hasError: this.props.hasError,
      inputValue: '',
      errortext: ''
    }
  }

  static getDerivedStateFromProps(props, current_state) {
    if (current_state.hasError!== props.hasError) {
      return {
        hasError: props.hasError
      }
    }
    return null
  }

  onChange(event) {
    let inputValue = event.target.value
    this.setState({ 
      inputValue: inputValue
    })
    if ( isEmpty(inputValue) ){
      this.setState({ 
        hasError: false
      })
    }
  }

  onFocus(event) {
    if (this.props.scrollIntoView) {
      let el = event.target
      el.scrollIntoView(true)
    }
  }

  render() {
    let className = classNames({
      'form-control': true,
      'error': this.state.hasError
    })
    return (
      <div className={ className } >
        <label>{ this.props.label }
          <input 
            type={this.props.type || 'text'} 
            className={ this.props.inputClassname } 
            placeholder={this.props.placeholder} 
            value={this.state.inputValue}
            onChange={this.onChange.bind(this)}
            onFocus={this.onFocus.bind(this)}
            onBlur={this.props.onInputBlur.bind(this)}
            name={this.props.name} 
            ref={this.props.inputRef}
            aria-describedby={`${this.props.name}-helptext`}/>
        </label>
        <OptionallyDisplayed display={this.state.hasError}>
          <Popover 
            id = {this.props.name}
            className = "bottom"
            ariaHidden = {!!this.state.hasError}
            isOpen = {this.state.hasError} 
            role = "alert" 
            text = {this.props.errorText} 
            />
        </OptionallyDisplayed>
        <p className="help-text" id={`${this.props.name}-helptext`}>{this.props.helpText}</p>  
      </div>
    )
  }
}

FormControl.propTypes = {
  type: PropTypes.string, 
  placeholder: PropTypes.string, 
  inputValue: PropTypes.string,
  text: PropTypes.string, 
  name: PropTypes.string, 
  errorText: PropTypes.string,
  inputClassname:  PropTypes.string,
  onInputBlur: PropTypes.func,
  onFieldChanged: PropTypes.func,
  hasError: PropTypes.bool,
  scrollIntoView: PropTypes.bool
}

FormControl.defaultProps = {
  type: 'text',
  placeholder: '',
  text: '',
  name: '',
  onInputBlur: function() {},
  hasError: false
}



