import React from 'react'

function ServicesAndEquipmentItem(props) {
    const {
      DisplayName
    } = props.item
 
    return (
      <tr>
        <td colSpan="3">{DisplayName}</td>
        <td>&nbsp;</td>
      </tr>
    )
  }

export default ServicesAndEquipmentItem



