import React, { Component } from 'react';
import PropTypes from 'prop-types'
import OrderItemBundleContainer from './../containers/OrderItemBundle'

export default class OrderItemBundleController extends Component {
  constructor(props){
    super(props)
    document.title = 'My Order Item'
  }

  render() {
    return <OrderItemBundleContainer {...this.props} />
  }
}

OrderItemBundleController.propTypes = {
  match: PropTypes.shape({
    params: PropTypes.shape({
      id: PropTypes.string.isRequired,
      itemId: PropTypes.string.isRequired
    }).isRequired
  }).isRequired
}



